import React, {Component} from "react";
import LabeledInput from "../../../core/components/hoc/LabeledInput";
import {getCommonPropsForInput} from "./common";

export default class Grantor extends Component {

    constructor(props) {
        super(props);
        this.ordinals = ["Pierwsza", "Druga", "Trzecia", "Czwarta", "Piąta", "Szósta", "Siódma", "Ósma", "Dziewiąta", "Dziesiąta"];
    }

    getLabel() {
        if(this.props.useLabel) {
            return this.ordinals[this.props.index] + " osoba udzielająca pełnomocnictwa";
        } else {
            return null;
        }
    }

    renderHeader() {
        return this.props.useLabel && (
            <div className="u-spacing-top-xl">
                <div className="o-separator u-spacing-m"/>
                <div className="u-position-relative">
                    <p className="h5 u-inline-block u-small-w-75 u-medium-w-75">{this.getLabel()}</p>
                    {this.props.remove && <a href="#" className="u-inline-block u-right u-small-right" onClick={this.remove.bind(this)}>Usuń</a>}
                </div>
            </div>
        );
    }

    remove(event) {
        event.preventDefault();
        this.props.remove(this.props.index);
    }

    render() {
        return (
            <div className="u-padding-l u-spacing-top-l">
                {this.renderHeader()}
                <div className="l-row">
                    <div className="l-col l-col-12">
                        <LabeledInput {...getCommonPropsForInput(this.props, "firstName", "grantor", {label:"Imię"})} />
                    </div>
                    <div className="l-col l-col-12 u-spacing-top-l">
                        <LabeledInput {...getCommonPropsForInput(this.props, "lastName", "grantor", {label:"Nazwisko"})} />
                    </div>
                </div>
            </div>
        );
    }
}